<template>
  <div v-if="songs" class="albumplaylist">
    <div class="topheaders" :style="'background-image: url('+data.album.blurPicUrl+')'">
      <div class="button" @click="$router.go(-1)">
        <span class="iconfont icon-zuojiantou"></span>
      </div>
      <div class="contents">
        <div class="img">
          <img :src="data.album.blurPicUrl" alt="" />
        </div>
        <div class="info">
          <h2>{{ data.album.name }}</h2>
          <h3>
            {{ data.album.description }}
          </h3>
        </div>
      </div>
      <div class="floor" :style="'background-image: url('+data.album.blurPicUrl+')'"></div>
    </div>
    <dl class="musicList">
      <dt>
        <van-icon name="play-circle-o" />播放全部<span
          >(共{{ songs.length }}首)</span
        >
      </dt>
      <dd v-for="(item, index) in songs" :key="item.id">
        <h1>
          {{ index + 1 }}
        </h1>
        <div class="info" @click="setId(item.id)">
          <h2>{{ item.name }}</h2>
          <h3>
            <span v-for="value in item.ar" :key="value.id"
              >{{ value.name }}&nbsp;</span
            >
          </h3>
        </div>
        <div class="icon">
          <van-icon
            name="like-o"
            :class="{ red: like.indexOf(item.id) != -1 }"
            @click="likes(item.id)"
          />
          <van-icon @click="showPopup(item.id)" name="more-o" />
        </div>
      </dd>
    </dl>

    <!-- 单曲菜单 -->
    <van-popup
      v-model="show"
      round
      position="bottom"
      :style="{ height: '60%' }"
    >
      <template>
        <div class="single-menu">
          <div class="top">
            <div class="img">
              <img :src="songsal.al.picUrl" alt="" />
            </div>
            <div class="text">
              <h2>歌曲:{{ songsal.al.name }}</h2>
              <h3>
                <span>{{ songsal.alia[0] }}</span>
              </h3>
            </div>
          </div>
          <ul class="content">
            <li>
              <i class="iconfont icon-Playlists-xiayishoubofang"></i>
              <span @click="push">添加到播放列表</span>
            </li>
            <li>
              <van-icon name="like-o" />
              <span>收藏到默认歌单</span>
            </li>
            <li>
              <van-icon name="down" />
              <span>下载</span>
            </li>
            <li>
              <van-icon name="chat-o" />
              <span>评论</span>
            </li>
            <li>
              <i class="iconfont icon-fenxiang"></i>
              <span>分享</span>
            </li>
            <li>
              <i class="iconfont icon-geshou"></i>
              <span
                >歌手: <p v-for="item in songsal.ar" :key="item.id">{{ item.name }}</p>
              </span>
            </li>
            <li>
              <i class="iconfont icon-zhuanji"></i>
              <span>专辑: </span>
            </li>
            <li>
              <van-icon name="cart-o" />
              <span>购买单曲</span>
            </li>
          </ul>
        </div>
      </template>
    </van-popup>
  </div>
</template>

<script>
import { getalbum } from "../api/rank";
import { getSongDetail, getlikelist, getlike } from "../api/base";
import { Toast } from "vant";
export default {
  data() {
    return {
      show: false,
      songs: [],
      data: {
        album: {
          blurPicUrl: "",
          name: "",
          description: "",
        },
      },
      songsal: {
        al: {
          picUrl: "",
          name: "",
        },
        alia: ["0"],
        ar: [],
      },
      id: null, //单曲数据
      like: [],
      likeid: null,
      collectionid: null, //收藏id
    };
  },

  created() {
    let queryId = this.$route.query;
    if (!("id" in queryId)) {
      this.$router.push("/rank");
    }
    getalbum({ id: queryId.id }).then((data) => {
      this.data = data;
      this.songs = data.songs;
    });
    getlikelist({ uid: window.localStorage.getItem("uid") }).then((data) => {
      this.like = data.ids;
    });
  },
  methods: {
    showPopup(id) {
      this.show = true;
      getSongDetail({ ids: id }).then((data) => {
        this.id = data.songs[0];
        this.songsal = data.songs[0];
        this.collectionid = data.songs[0].id;
      });
    },
    setId(id) {
      // 播放全部歌曲
      this.$emit("get-play-all-id", this.$route.query.id); //传递歌单id
      this.$emit("get-player-id", id);
    },
    //传参,用于添加播放列表
    push() {
      this.$emit("push", this.id);
      Toast("添加成功");
    },
    //喜欢,不喜欢
    likes(id) {
      if (this.like.indexOf(id) != -1) {
        getlike({ id: id, like: false }).then(() => {
          this.like.splice(this.like.indexOf(id), 1);
          Toast("取消收藏");
        });
      } else {
        getlike({ id: id }).then(() => {
          this.like.push(id);
          Toast("收藏成功");
        });
      }
    },
  },
};
</script>

